import React, { useState, useEffect } from 'react';
import { YMaps, Map, Placemark } from '@pbe/react-yandex-maps';
import { useDispatch, useSelector } from 'react-redux';
import { sendOrder } from '../redux/actions/order';
import { PaymentModal } from '../components';

function Order() {
  const dispatch = useDispatch();
  const { totalPrice, totalCount, items } = useSelector(({ cart }) => cart);
  const userDetails = useSelector((state) => state.verification);

  const [mapState, setMapState] = useState({
    center: [47.22360251653857, 39.719324054985094],
    zoom: 12,
  });
  const [placemark, setPlacemark] = useState(null);
  const [address, setAddress] = useState('');
  const [coordinates, setCoordinates] = useState([null, null]);
  const [name, setName] = useState('');
  const [phone, setPhone] = useState('');
  const [suggestions, setSuggestions] = useState([]);
  const [isRussianAddress, setIsRussianAddress] = useState(false);


  // При монтировании компонента устанавливаем данные из Redux
  useEffect(() => {
    if (userDetails) {
      setName(userDetails.name || '');
      setPhone(userDetails.phoneNumber || '');

      const addr = userDetails.address || '';
      setAddress(addr);

      if (addr) {
        // Обратное геокодирование адреса в координаты
        (async () => {
          try {
            const response = await fetch(
              `https://geocode-maps.yandex.ru/1.x/?apikey=17832c8b-6141-48f8-8f39-7b51697f537a&geocode=${encodeURIComponent(addr)}&format=json`
            );
            const data = await response.json();
            const featureMember = data?.response?.GeoObjectCollection?.featureMember;
            if (featureMember && featureMember.length > 0) {
              const geoObject = featureMember[0].GeoObject;
              const pos = geoObject.Point.pos.split(' ').map(Number);
              const coords = [pos[1], pos[0]]; // Поменять местами
              setCoordinates(coords);
              setPlacemark(coords);
              setMapState((prev) => ({ ...prev, center: coords }));

              // Проверка страны
              const country = geoObject.metaDataProperty.GeocoderMetaData.AddressDetails.Country.CountryName;
              setIsRussianAddress(country === 'Россия');
            }

          } catch (error) {
            console.error('Ошибка обратного геокодирования:', error);
          }
        })();
      }
    }
  }, [userDetails]);

  const handleMapClick = async (event) => {
    const coords = event.get('coords');

    try {
      const response = await fetch(
        `https://geocode-maps.yandex.ru/1.x/?apikey=17832c8b-6141-48f8-8f39-7b51697f537a&geocode=${coords[1]},${coords[0]}&format=json`
      );
      const data = await response.json();

      if (data && data.response && data.response.GeoObjectCollection.featureMember.length > 0) {
          const geoObject = data.response.GeoObjectCollection.featureMember[0].GeoObject;
          const foundAddress = geoObject.metaDataProperty.GeocoderMetaData.Address.formatted;
          const country = geoObject.metaDataProperty.GeocoderMetaData.AddressDetails.Country.CountryName;
          setAddress(foundAddress);
          setCoordinates(coords);
          setPlacemark(coords);
          setIsRussianAddress(country === 'Россия');
        } else {
          setAddress('Адрес не найден');
          setIsRussianAddress(false);
        }

    } catch (error) {
      setAddress('Ошибка при определении адреса');
      console.error(error);
    }
  };

  const handleAddressChange = async (e) => {
    const value = e.target.value;
    setAddress(value);

    if (value) {
      try {
        const response = await fetch(
          `https://geocode-maps.yandex.ru/1.x/?apikey=17832c8b-6141-48f8-8f39-7b51697f537a&geocode=${encodeURIComponent(value)}&format=json&results=5`
        );
        const data = await response.json();
        const suggestions = data.response.GeoObjectCollection.featureMember.map((feature) => ({
          address: feature.GeoObject.metaDataProperty.GeocoderMetaData.Address.formatted,
          coords: feature.GeoObject.Point.pos.split(' ').map(Number).reverse(), // [широта, долгота]
        }));
        setSuggestions(suggestions);
      } catch (error) {
        console.error('Ошибка при получении подсказок:', error);
      }
    } else {
      setSuggestions([]);
    }
  };

  const handleSuggestionClick = (suggestion) => {
  setAddress(suggestion.address);
  setCoordinates(suggestion.coords);
  setPlacemark(suggestion.coords);
  setMapState((prev) => ({ ...prev, center: suggestion.coords }));

  // Обратное геокодирование для проверки страны
  (async () => {
    try {
      const response = await fetch(
        `https://geocode-maps.yandex.ru/1.x/?apikey=17832c8b-6141-48f8-8f39-7b51697f537a&geocode=${suggestion.coords[1]},${suggestion.coords[0]}&format=json`
      );
      const data = await response.json();
      if (data.response.GeoObjectCollection.featureMember.length > 0) {
        const geoObject = data.response.GeoObjectCollection.featureMember[0].GeoObject;
        const country = geoObject.metaDataProperty.GeocoderMetaData.AddressDetails.Country.CountryName;
        setIsRussianAddress(country === 'Россия');
      } else {
        setIsRussianAddress(false);
      }
    } catch (error) {
      setIsRussianAddress(false);
      console.error(error);
    }
  })();

  setSuggestions([]); // Убираем подсказки
};


  const [isModalOpen, setModalOpen] = useState(false);

    const openModal = () => {
        setModalOpen(true);
    };

    const closeModal = () => {
        setModalOpen(false);
    };

  const handleOrderSubmit = () => {
    const orderData = {
      recipientName: name,
      recipientPhone: phone,
      recipientAddress: address,
      products: items,
      totalQuantity: totalCount,
      totalPrice: totalPrice,
    };

    dispatch(sendOrder(orderData));
    openModal();
  };






  return (
    <div className="order-form">
      <h2 className="form-title">Оформление заказа</h2>
      <div className="form-group">
        <label htmlFor="name" className="form-label">
          Имя:
        </label>
        <input
          type="text"
          id="name"
          className="form-input"
          placeholder="Введите ваше имя"
          value={name}
          onChange={(e) => setName(e.target.value)}
          required
        />
      </div>
      <div className="form-group">
        <label htmlFor="phone" className="form-label">
          Номер телефона:
        </label>
        <input
          type="tel"
          id="phone"
          className="form-input"
          placeholder="Введите номер телефона"
          value={phone}
          onChange={(e) => setPhone(e.target.value)}
          maxLength={10}
          required
        />
      </div>
      <YMaps query={{ apikey: '17832c8b-6141-48f8-8f39-7b51697f537a' }}>
        <div className="form-group">
          <label htmlFor="address" className="form-label">
            Адрес доставки:
          </label>
          <input
            type="text"
            value={address}
            onChange={handleAddressChange}
            placeholder="Введите адрес"
            className="form-input"
            required
          />
          {suggestions.length > 0 && (
            <ul className="map-suggestions-list">
              {suggestions.map((suggestion, index) => (
                <li key={index} onClick={() => handleSuggestionClick(suggestion)}>
                  {suggestion.address}
                </li>
              ))}
            </ul>
          )}
          <Map
            state={mapState}
            onClick={handleMapClick}
            style={{ width: '100%', height: '400px' }}
          >
            {placemark && <Placemark geometry={placemark} />}
          </Map>

          <div
            className={`button pay-btn ${!isRussianAddress ? 'disabled' : ''}`}
            onClick={isRussianAddress ? handleOrderSubmit : undefined}
            style={{ cursor: isRussianAddress ? 'pointer' : 'not-allowed', opacity: isRussianAddress ? 1 : 0.5 }}
          >
            <span>Перейти к оплате</span>
          </div>
          {!isRussianAddress && <p style={{ color: 'red' }}>Измените адрес доставки в пределах России</p>}

        </div>
      </YMaps>
      <PaymentModal isOpen={isModalOpen} onRequestClose={closeModal} />
    </div>
  );
}

export default Order;
