import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { setUserDetails, saveUserDetails, userExit } from '../redux/actions/verification';
import { YMaps, Map, Placemark, ZoomControl, SearchControl } from '@pbe/react-yandex-maps';
import { useNavigate } from 'react-router-dom';

const Profile = () => {
  const dispatch = useDispatch();
  const userDetails = useSelector((state) => state.verification);

  const [name, setName] = useState('');
  const [email, setEmail] = useState('');
  const [phone, setPhone] = useState('');
  const [address, setAddress] = useState('');
  const [placemark, setPlacemark] = useState(null);
  const [mapState, setMapState] = useState({
    center: [47.22360251653857, 39.719324054985094],
    zoom: 12,
  });
  const [suggestions, setSuggestions] = useState([]);
  const [isRussianAddress, setIsRussianAddress] = useState(false);


  const checkIsRussianAddress = (geoObject) => {
  try {
    const components = geoObject.metaDataProperty.GeocoderMetaData.Address.Components;
    // Ищем компонент с kind === 'country' и name === 'Россия'
    return components.some(c => c.kind === 'country' && c.name === 'Россия');
  } catch {
    return false;
  }
};

  // При загрузке компонента устанавливаем данные из Redux
  useEffect(() => {
    if (userDetails) {
      setName(userDetails.name || '');
      setEmail(userDetails.email || '');
      setPhone(userDetails.phoneNumber || '');
      const addr = userDetails.address || '';
      setAddress(addr);

      if (addr) {
        // Обратное геокодирование: получаем координаты по адресу
        (async () => {
          try {
            const response = await fetch(
              `https://geocode-maps.yandex.ru/1.x/?apikey=17832c8b-6141-48f8-8f39-7b51697f537a&geocode=${encodeURIComponent(addr)}&format=json`
            );
            const data = await response.json();
            const featureMember = data?.response?.GeoObjectCollection?.featureMember;
            if (featureMember && featureMember.length > 0) {
                const geoObject = featureMember[0].GeoObject;
                const pos = geoObject.Point.pos.split(' ').map(Number);
                const coords = [pos[1], pos[0]];
                setPlacemark(coords);
                setMapState((prev) => ({ ...prev, center: coords }));

                // Проверяем страну
                setIsRussianAddress(checkIsRussianAddress(geoObject));
              } else {
                setIsRussianAddress(false);
              }

          } catch (error) {
            console.error('Ошибка обратного геокодирования:', error);
          }
        })();
      }
    }
  }, [userDetails]);

  const handleMapClick = async (event) => {
    const coords = event.get('coords');

    try {
      const response = await fetch(
        `https://geocode-maps.yandex.ru/1.x/?apikey=17832c8b-6141-48f8-8f39-7b51697f537a&geocode=${coords[1]},${coords[0]}&format=json`
      );
      const data = await response.json();

      if (data && data.response && data.response.GeoObjectCollection.featureMember.length > 0) {
        const geoObject = data.response.GeoObjectCollection.featureMember[0].GeoObject;
        const foundAddress = geoObject.metaDataProperty.GeocoderMetaData.Address.formatted;
        setAddress(foundAddress);
        setIsRussianAddress(checkIsRussianAddress(geoObject));
      } else {
        setAddress('Адрес не найден');
        setIsRussianAddress(false);
      }

    } catch (error) {
      setAddress('Ошибка при определении адреса');
      console.error(error);
    }

    setPlacemark(coords);
  };

  const handleAddressChange = async (e) => {
    const value = e.target.value;
    setAddress(value);

    if (value) {
      try {
        const response = await fetch(
          `https://geocode-maps.yandex.ru/1.x/?apikey=17832c8b-6141-48f8-8f39-7b51697f537a&geocode=${encodeURIComponent(value)}&format=json&results=5`
        );
        const data = await response.json();
        const suggestions = data.response.GeoObjectCollection.featureMember.map((feature) => ({
          address: feature.GeoObject.metaDataProperty.GeocoderMetaData.Address.formatted,
          coords: feature.GeoObject.Point.pos.split(' ').map(Number).reverse(), // [широта, долгота]
        }));
        setSuggestions(suggestions);
      } catch (error) {
        console.error('Ошибка при получении подсказок:', error);
      }
    } else {
      setSuggestions([]);
    }
  };

  const handleSuggestionClick = (suggestion) => {
    setAddress(suggestion.address);
    setPlacemark(suggestion.coords);
    setMapState((prev) => ({ ...prev, center: suggestion.coords }));

    // Нужно получить геоданные по адресу, чтобы проверить страну
    (async () => {
      try {
        const response = await fetch(
          `https://geocode-maps.yandex.ru/1.x/?apikey=17832c8b-6141-48f8-8f39-7b51697f537a&geocode=${encodeURIComponent(suggestion.address)}&format=json&results=1`
        );
        const data = await response.json();
        if (data && data.response.GeoObjectCollection.featureMember.length > 0) {
          const geoObject = data.response.GeoObjectCollection.featureMember[0].GeoObject;
          setIsRussianAddress(checkIsRussianAddress(geoObject));
        } else {
          setIsRussianAddress(false);
        }
      } catch {
        setIsRussianAddress(false);
      }
    })();

    // Обновляем адрес в Redux
    dispatch(setUserDetails({ name, email, phoneNumber: phone, address: suggestion.address }));
    setSuggestions([]);

  };

  const handleSubmit = async (e) => {
      e.preventDefault();
      await dispatch(saveUserDetails({ name, email, phoneNumber: phone, address }));
      alert('Данные обновлены!')
    };

  const navigate = useNavigate()
  const handleUserExit = () => {
    const confirmExit = window.confirm("Вы уверены, что хотите выйти из аккаунта?");
    if (confirmExit) {
        dispatch(userExit());
        navigate ('/')
    }
};

  return (
    <div className="profile-container">
      <h1 className="profile-title">Личный кабинет
        <button type="button" onClick={handleUserExit} className="exit-button">
          Выйти
        </button>
      </h1>
      
      <form className="profile-form" onSubmit={handleSubmit}>
        <div className="form-group">
          <label htmlFor="name">Имя</label>
          <input
            type="text"
            id="name"
            name="name"
            value={name}
            onChange={(e) => setName(e.target.value)}
            required
          />
        </div>
        <div className="form-group">
          <label htmlFor="email">E-mail</label>
          <input
            type="email"
            id="email"
            name="email"
            value={email}
            onChange={(e) => setEmail(e.target.value)}
            required
          />
        </div>
        <div className="form-group">
          <label htmlFor="phone">Номер телефона</label>
          <input
            type="tel"
            id="phone"
            name="phone"
            value={phone}
            onChange={(e) => setPhone(e.target.value)}
            maxLength={10}
            required
          />
        </div>
        <div className="form-group">
          <label htmlFor="address" className="form-label">
            Адрес доставки:
          </label>
          <input
            type="text"
            id="address"
            name="address"
            value={address}
            onChange={handleAddressChange}
            placeholder="Введите адрес"
            className="form-input"
            required
          />
          {suggestions.length > 0 && (
            
            <ul className="map-suggestions-list">
              {suggestions.map((suggestion, index) => (
                <li key={index} onClick={() => handleSuggestionClick(suggestion)}>
                  {suggestion.address}
                </li>
              ))}
            </ul>
          )}
        </div>
        <YMaps query={{ apikey: '17832c8b-6141-48f8-8f39-7b51697f537a' }}>
          <Map
            state={mapState}
            style={{ width: '100%', height: '500px' }}
            onClick={handleMapClick}
          >
            <ZoomControl />
            {placemark && (
              <Placemark
                geometry={placemark}
                properties={{
                  balloonContent: `Адрес: ${address}`,
                }}
              />
            )}
          </Map>
        </YMaps>
        <div
            className={`button pay-btn ${!isRussianAddress ? 'disabled' : ''}`}
            onClick={isRussianAddress ? handleSubmit : undefined}
            style={{ cursor: isRussianAddress ? 'pointer' : 'not-allowed', opacity: isRussianAddress ? 1 : 0.5 }}
          >
            <span>Сохранить</span>
          </div>
          {!isRussianAddress && <p style={{ color: 'red' }}>Измените адрес доставки в пределах России</p>}

        
      </form>
    </div>
  );
};

export default Profile;
